import { useEffect } from "react";
import { SpinePlayer } from "@esotericsoftware/spine-player";
import "@esotericsoftware/spine-player/dist/spine-player.css";
import s from "./spineAnimation.module.css";

const SpineAnimation = ({ rotate }) => {
  useEffect(() => {
    setTimeout(() => {
      const spineRoot = document.querySelector("#player-container");
      new SpinePlayer(spineRoot, {
        jsonUrl: "ai_girl.json",
        atlasUrl: "ai_girl.atlas",
        rawDataURIs: {
          "ai_girl.json":
            "data:application/json;base64,",
          "ai_girl.atlas":
            "data:application/octet-stream;base64,",
          "ai_girl.png":
            "data:image/png;base64,",
        },
        animation: "idle_anim",
        showControls: false,
        backgroundColor: "#00000000",
        alpha: true,
        // viewport: {
        //   x: (898 / 2) * -1,
        //   y: 0,
        //   width: 898,
        //   height: 1321,
        // },
      });

      //   spineRoot.classList.add(s.spineContainer);
    }, 100);
  }, []);

  return (
    <div
      id="player-container"
      className={`${s.spineContainer} ${rotate && s.changeZIndex}`}
    ></div>
  );
};
export default SpineAnimation;
